import { mapGetters } from 'vuex'

//  mixin 
export default {
  computed: {
    ...mapGetters('m_cart', ['total']),
  },
  onShow() {
    // 
    this.setBadge()
  },
  watch: {
    //  total 
    total() {
      //  methods  setBadge  tabBar 
      this.setBadge()
    },
  },
  methods: {
    setBadge() {
      //  uni.setTabBarBadge() 
      uni.setTabBarBadge({
        index: 3,
        text: this.total + '', // text 
      })
    },
  },
}
