export default {
  // 
  namespaced: true,

  // state 
  state: () => ({
    // 
    address: JSON.parse(uni.getStorageSync('address') || '{}'),
    //  token 
    token: uni.getStorageSync('token') || '',
    // 
    userinfo: JSON.parse(uni.getStorageSync('userinfo') || '{}'),
    //  object  { openType, from }
    redirectInfo: null,
    my: JSON.parse(uni.getStorageSync('my') || '{}'),
  }),

  // 
  mutations: {
    // 
    updateAddress(state, address) {
      state.address = address
      // 2.  this.commit()  m_user  saveAddressToStorage  address 
      this.commit('m_user/saveAddressToStorage')
    },
    // 1.  address  mutations 
    saveAddressToStorage(state) {
      state.my.my_phone = state.address.telNumber
      uni.setStorageSync('address', JSON.stringify(state.address))
    },

    // 
    updateUserInfo(state, userinfo) {
      state.userinfo = userinfo
      //  this.commit()  m_user  saveUserInfoToStorage  userinfo 
      this.commit('m_user/saveUserInfoToStorage')
    },
    //  userinfo 
    saveUserInfoToStorage(state) {
      uni.setStorageSync('userinfo', JSON.stringify(state.userinfo))
    },

    //  token 
    updateToken(state, token) {
      state.token = token
      //  this.commit()  m_user  saveTokenToStorage  token 
      this.commit('m_user/saveTokenToStorage')
    },
    //  token 
    saveTokenToStorage(state) {
      uni.setStorageSync('token', state.token)
    },
    // 
    updateRedirectInfo(state, info) {
      state.redirectInfo = info
    },
    setMy(state, info) {
      state.my = info
      this.commit('m_user/saveMy')
    },
    saveMy(state) {
      uni.setStorageSync('my', JSON.stringify(state.my))
    },
  },

  // 
  getters: {
    // 
    addstr(state) {
      if (!state.address.provinceName) return ''

      //   
      return (
        state.address.provinceName +
        state.address.cityName +
        state.address.countyName +
        state.address.detailInfo
      )
    },
  },
}
